:root {
  --main-color: #4FD666;
  --main-bg-gradient: linear-gradient(to top right, #3EC97A, #69EA49 20%, rgba(255, 255, 255, 0) 80%, rgba(255, 255, 255, 0));
  --border-color: green;
  --main-bg-color: white;

  --vk-bg-color: #edeef0;
  
}

.all {
  
}

.app-wrapper {  
  background-color: var(--main-bg-color);
  border-bottom: var(--border-color) 1px solid; 
}

.app-wrapper-content {
  grid-area: content;
  background-color: var(--main-bg-color);
  min-height: 1000px;
  padding: 10px;
  
}

.app-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 15px;
  display: grid;
  

  grid-template-areas:
  "nav content"
  "foot foot";
    grid-template-columns: 154px 1fr;
    grid-template-rows: 1fr 48px;
  
}